.mainView {
	justify-content: center;
}

.mainView {
	display: grid;
}

.ControlPanel {
	display: flex;
	align-items: center;
	padding: 10px;
	margin: auto;
}

.plotWrapper {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.select {
	max-width: 15vw;
	margin-left: 5px;
	margin-right: 10px;
}

.brushed {
	stroke: black;
}

.TableView {
	display: inline-block;
	box-sizing: border-box;
	z-index: 0;
}